const _hoisted_12 = /*#__PURE__*/_createStaticVNode("<div class=\"main-col1\" data-v-76065c04><div class=\"main-col1-text-box\" data-v-76065c04><div class=\"main-col1-text\" data-v-76065c04><h6 data-v-76065c04>Keelatud on:</h6><ul data-v-76065c04><li data-v-76065c04>alkoholi ja uimastite omamine ja tarbimine ürituse vältel</li><li data-v-76065c04>alkoholijoobes või uimastite mõju all viibimine üritusel</li><li data-v-76065c04>suitsetamine (ka e-sigaret) selleks mitte ettenähtud kohtades - suitsetada võib õues (koni maha ei viska)</li><li data-v-76065c04>külm- ja imitatsioonrelvad</li><li data-v-76065c04>ürituse ajal igasuguse vägivalla kasutamine teiste ja teiste vara suhtes</li><li data-v-76065c04>teiste vara omavoliline kasutamine, näppimine</li><li data-v-76065c04>turniiri reeglitele mittevastavalt mängimine</li><li data-v-76065c04>omavoliline taristu (võrgu) näppimine - võrguprobleemidega tuleb pöörduda korraldajate poole</li><li data-v-76065c04>mängimiseks ebavajalike seadmete ühendamine vooluvõrku (nt veekeetja, puhur, sirgendaja)</li><li data-v-76065c04>iga külastaja vastutab enda asjade ja vara eest ise</li><li data-v-76065c04>korraldajale varalise kahju tekitanud külastaja on kohustatud maksma täies ulatuses tekitatud kahju</li><li data-v-76065c04>korraldajad ei vastuta külastajate eest</li></ul><p data-v-76065c04>NB! Reeglite rikkumise puhul on korraldajatel õigus mängija ürituselt minema saata</p></div></div></div><div class=\"main-col2\" data-v-76065c04><div class=\"main-col2-text-box\" data-v-76065c04><div class=\"main-col2-text\" data-v-76065c04><h6 data-v-76065c04>Kuidas ma saan pileti eest tasuda?</h6><p data-v-76065c04>Veebilehel on võimalik tasuda tiimijuhil tiimipilet 25€ (5 inimest).</p><ul data-v-76065c04><li data-v-76065c04>Piletite ostmine käib fienta keskkonna kaudu</li><li data-v-76065c04>Ostmisel tuleb lisada tiiminimi ja -liikmed</li><li data-v-76065c04>Liikmete muutumisel palun ühendust võtta <u data-v-76065c04>sander.pluks@ituk.ee</u>. Liikmeid saab muuta 9. juunini</li><li data-v-76065c04>Turniiri kohast loobumisel saab raha tagasi kuni 27. mai 18:00. Kohast loobumisel palun sellest korraldajaid teavitada</li></ul><h6 data-v-76065c04>Millal on minu meeskonnale kohad garanteeritud?</h6><p data-v-76065c04>Kohad reserveeritakse kohe pärast makse laekumist.</p><h6 data-v-76065c04>Mida peaksin üritusele kaasa võtma?</h6><p data-v-76065c04>Arvuti koos kõige muu vajalikuga (monitor, hiir, klaviatuur) ja pikendusjuhtme.</p><h6 data-v-76065c04>Mis mind kohapeal ootab?</h6><p data-v-76065c04>Lauapind ja tool, võrgukaabel, üks vooluvõtu koht. OMA PIKENDUSJUHE KAASA!</p><h6 data-v-76065c04>Mul ei ole meeskonda, kas saan osaleda ka üksinda?</h6><p data-v-76065c04>CS:GO turniiril üksinda osaleda ei saa. Teistel külastajatel pole meeskonda vaja.</p></div></div></div><div class=\"main-timetable\" data-v-76065c04><div class=\"main-timetable-text-box\" data-v-76065c04><div class=\"main-timetable-title1\" data-v-76065c04><div data-v-76065c04><h6 data-v-76065c04>Ajakava</h6></div></div><div class=\"main-timetable-text\" data-v-76065c04><p data-v-76065c04>Reede, 10. juuni</p><p data-v-76065c04>18:00: Uste avamine</p><br data-v-76065c04><p data-v-76065c04>18:00-20:00: set up ja warm-up</p><br data-v-76065c04><p data-v-76065c04>19:30-21:30: Dirt Rally 2.0 turniir</p><br data-v-76065c04><p data-v-76065c04>20:00- ...: CSGO turniir</p></div><hr data-v-76065c04><div class=\"main-timetable-text\" data-v-76065c04><p data-v-76065c04>Laupäev, 11. juuni</p><p data-v-76065c04>... -03:00: CSGO turniir</p><br data-v-76065c04><p data-v-76065c04>03:00-04:00: vaba kava</p></div></div></div>", 3)

